body {
  background: #eee;
  font-family: Segoe UI, Tahoma, Verdana, sans-serif;
  line-height: 1.7;
}

.container {
  max-width: 1700px;
  margin: 0 auto;
}

.loader {
  width: 32px;
  height: 32px;
  text-align: center;
  color: #fcdb03;
  border-radius: 50%;
  animation: fill 1s ease-in infinite alternate;
  display: flex;
  position: absolute;
}

.loader:before, .loader:after {
  content: "";
  height: 100%;
  width: 100%;
  border-radius: 50%;
  animation: fill .9s ease-in infinite alternate;
  position: absolute;
  top: 0;
  left: 50px;
}

.loader:after {
  animation-duration: 1.1s;
  left: auto;
  right: 48px;
}

@keyframes fill {
  0% {
    box-shadow: inset 0 0 0 2px;
  }

  100% {
    box-shadow: inset 0 0 0 10px;
  }
}

.breed-select {
  width: 50%;
  color: #00008b;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 10px;
}

.cat-info {
  max-width: 1700px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin: 0 auto;
  display: flex;
}

.cat-list img {
  width: 100%;
  height: 300px;
}

.cat-list {
  width: 32%;
  float: left;
  margin: 7px;
}

.cat-descr {
  flex-direction: column;
  display: flex;
}

.name {
  font-size: 22px;
}

.description, .features {
  font-size: 16px;
}

